.container{
  max-width: 1300px;
  margin: auto;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;

}
.box{
  background-color: rgb(0, 0, 0);
  border: 5px solid rgb(77, 86, 209);
  border-radius: 20px;
  width: 25%;
  margin: 10px;
  padding: 20px;
  box-shadow: 2px 2px 10px white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: all 1s;

}
.box:hover{
  transform: scale(1.1);
  box-shadow: 0px 0px 5px 5px purple;
}
h5{
  font-size:x-large;
