}
p{
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: palegreen;
  font-size: 100%;
}
img{
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: end;
